@import url(https://fonts.googleapis.com/css?family=Patua+One&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.playlistContainer {
	border-radius: 9px;
	width: 300px;
	height: 380px;
	overflow: hidden;
  display: inline-block;
  position: relative;
}

.iframeElement {
    border: 0;
  	width: 100%;
  	height: 100%; 
}
* {
  margin: 0;
  padding: 0;
}

.Logo {
  position: absolute;
  left: 20px;
  top: 15px;
  color: white;
  font-size: 20px;
  font-family: 'Patua One', cursive;
}

.Window {
  width: 100%; 
  height: 100vh;
  overflow: hidden; 
}

.Container {
  width: 100%; 
  height: 100vh;
  height: calc(1vh * 100);
  height: calc(var(--vh, 1vh) * 100);
  background-color: white; 
}

.Image {
  height: 100%;
  display: block;
  margin: auto;
  -webkit-filter: grayscale(0%) opacity(.8) blur(3px);
          filter: grayscale(0%) opacity(.8) blur(3px);
}

.Playlist {
  position: relative;
  text-align: center; 
  top: -28em;
}
